import React, { Component } from "react";

import "../CssComponents/BoxDrums.css";

import DrumButton from "./DrumButton";

export default class BoxDrums extends Component {
  state = {
    pressed: "",
    sounds: [
      {
        keyCode: 81,
        keyTrigger: "Q",
        id: "Heater-1",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3",
      },
      {
        keyCode: 87,
        keyTrigger: "W",
        id: "Heater-2",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3",
      },
      {
        keyCode: 69,
        keyTrigger: "E",
        id: "Heater-3",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3",
      },
      {
        keyCode: 65,
        keyTrigger: "A",
        id: "Heater-4",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3",
      },
      {
        keyCode: 83,
        keyTrigger: "S",
        id: "Clap",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3",
      },
      {
        keyCode: 68,
        keyTrigger: "D",
        id: "Open-HH",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3",
      },
      {
        keyCode: 90,
        keyTrigger: "Z",
        id: "Kick-n'-Hat",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3",
      },
      {
        keyCode: 88,
        keyTrigger: "X",
        id: "Kick",
        url: "https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3",
      },
      {
        keyCode: 67,
        keyTrigger: "C",
        id: "Closed-HH",
        url: "https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3",
      },
    ],
  };

  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyPress);
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyPress);
  }

  handleKeyPress = (e) => {
    for (let i = 0; i < this.state.sounds.length; i++) {
      if (e.key.toUpperCase() === this.state.sounds[i].keyTrigger) {
        this.playSound(this.state.sounds[i]);
      }
    }
  };

  playSound = (S) => {
    this.setState({
      pressed: S.id,
    });
    const sound = document.getElementById(S.keyTrigger);
    sound.play();
  };

  displayState = (newD) => {
    this.setState({
      pressed: newD,
    });
  };

  render() {
    return (
      <div id="drum-machine">
        <h1 id="title">Box Drums</h1>
        <h2 id="display">{this.state.pressed}</h2>
        {this.state.sounds.map((sound) => {
          return (
            <DrumButton
              key={sound.id}
              sound={sound}
              playSound={this.playSound}
              displayState={this.displayState}
            />
          );
        })}
      </div>
    );
  }
}
