import React, { Component } from "react";

import "../CssComponents/DrumButton.css";

export default class DrumButton extends Component {
  playSound = () => {
    const sound = document.getElementById(this.props.sound.keyTrigger);
    sound.play();
    this.props.displayState(this.props.sound.id);
  };
  render() {
    return (
      <div
        className="drum-pad"
        style={{
          gridArea: `button${this.props.sound.keyTrigger}`,
        }}
        onClick={this.playSound}
      >
        <audio
          id={this.props.sound.keyTrigger}
          src={this.props.sound.url}
        ></audio>
        {this.props.sound.keyTrigger}
      </div>
    );
  }
}
